import React, { useState } from "react";
import "./App.css";
import Header from "./components/Header";
import { Box, Paper, Container, Typography } from "@mui/material";
import FileUpload from "./components/FileUpload";

function App() {
  const [submitted, setSubmitted] = useState(false);
  const [uploadedFiles, setUploadedFiles] = useState<
    { score?: number; file: File }[]
  >([]);
  const [localFiles, setLocalFiles] = useState<
    { score?: number; file: File }[]
  >([]);

  return (
    <Box>
      <Header
        setSubmitted={setSubmitted}
        setUploadedFiles={setUploadedFiles}
        setLocalFiles={setLocalFiles}
      />
      <Box
        sx={{
          marginY: 2.5,
        }}
      >
        <Container style={{ maxWidth: "650px" }}>
          {!submitted ? (
            <>
              <Typography variant="h4" component="h1" textAlign="center">
                Instagram Photo Popularity Analyzer
              </Typography>
              <Typography
                variant="body1"
                component="p"
                paragraph
                textAlign="center"
              >
                Predict your photo's popularity and find out which ones will get
                the most likes
              </Typography>
              <Box>
                <FileUpload
                  setSubmitted={setSubmitted}
                  setUploadedFiles={setUploadedFiles}
                  setLocalFiles={setLocalFiles}
                  localFiles={localFiles}
                />
              </Box>
            </>
          ) : (
            <Container style={{ maxWidth: "450px" }}>
              <Box
                sx={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  justifyContent: "center",
                  gap: 2.5,
                }}
              >
                {uploadedFiles
                  .sort((a, b) => (b.score || 0) - (a.score || 0))
                  .map((fileData, index) => (
                    <Paper
                      key={fileData.file.name}
                      elevation={12}
                      sx={{
                        padding: 0,
                        width: "100%",
                        height: "auto",
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "center",
                        overflow: "hidden",
                      }}
                    >
                      <Box
                        sx={{
                          position: "relative",
                          width: "100%",
                          paddingTop: "75%",
                          overflow: "hidden",
                        }}
                      >
                        <img
                          src={URL.createObjectURL(fileData.file)}
                          alt={fileData.file.name}
                          style={{
                            position: "absolute",
                            top: 0,
                            left: 0,
                            width: "100%",
                            height: "100%",
                            objectFit: "cover",
                          }}
                        />
                      </Box>

                      <Typography variant="h6">{fileData.file.name}</Typography>
                      <Typography variant="body1">
                        Score:{" "}
                        {fileData.score !== undefined
                          ? Math.floor((fileData.score + 3) * 10)
                          : "N/A"}
                        %
                      </Typography>
                    </Paper>
                  ))}
              </Box>
            </Container>
          )}
        </Container>
      </Box>
    </Box>
  );
}

export default App;
