import * as React from "react";
import { useState, useEffect, ChangeEvent, DragEvent } from "react";
import { styled } from "@mui/material/styles";
import Button from "@mui/material/Button";
import CloudUploadIcon from "@mui/icons-material/CloudUpload";
import { Box, Typography, Chip, Paper } from "@mui/material";
import ImageOutlinedIcon from "@mui/icons-material/ImageOutlined";
import CircularProgress from "@mui/material/CircularProgress";

const VisuallyHiddenInput = styled("input")`
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  bottom: 0;
  left: 0;
  white-space: nowrap;
  width: 1px;
`;

const StyledButton = styled(Button as any)`
  width: 100%;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px dashed #ccc;
  background-color: #fff;
  color: black;
  flex-direction: column;
  text-transform: none;
  &:hover {
    background-color: #f9e6f3;
    border: 2px dashed #c13584;
    color: #c13584;
  }
`;

interface FileUploadProps {
  setSubmitted: React.Dispatch<React.SetStateAction<boolean>>;
  setUploadedFiles: React.Dispatch<
    React.SetStateAction<{ score?: number; file: File }[]>
  >;
  localFiles: { score?: number; file: File }[];
  setLocalFiles: React.Dispatch<
    React.SetStateAction<{ score?: number; file: File }[]>
  >;
}

export default function FileUpload({
  setSubmitted,
  setUploadedFiles,
  setLocalFiles,
  localFiles,
}: FileUploadProps) {
  useEffect(() => {
    window.scroll({
      top: document.body.scrollHeight,
      behavior: "smooth",
    });
  }, [localFiles]);

  const [loading, setLoading] = useState(false);

  const handleFileUpload = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const files = Array.from(e.target.files);
      updateFileList(files);
    }
  };

  const handleDrop = (e: DragEvent<HTMLButtonElement>) => {
    e.preventDefault();
    if (e.dataTransfer.items) {
      const files = Array.from(e.dataTransfer.files);
      updateFileList(files);
    }
  };

  const updateFileList = (files: File[]) => {
    const fileData = files.map((file) => ({
      file: file,
    }));
    setLocalFiles((prevFiles) => [...prevFiles, ...fileData]);
  };

  const removeFile = (index: number) => {
    setLocalFiles((prevFiles) => {
      const newFiles = [...prevFiles];
      newFiles.splice(index, 1);
      return newFiles;
    });
  };

  const handleSubmit = () => {
    setLoading(true);
    const formData = new FormData();
    localFiles.forEach((fileData) => {
      if (fileData.file) {
        formData.append("file", fileData.file, fileData.file.name);
      }
    });

    // POST to Flask endpoint
    fetch("/api", {
      method: "POST",
      body: formData,
    })
      .then((response) => response.json())
      .then((result) => {
        const scores = result["Popularity scores"];
        const updatedFiles = localFiles.map((fileData) => ({
          ...fileData,
          score: scores[fileData.file.name],
        }));
        setUploadedFiles(updatedFiles);
        window.scrollTo(0, 0);
        setSubmitted(true);
      })
      .catch((error) => console.log("error", error))
      .finally(() => {
        setLoading(false);
      });
  };

  return (
    <>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <StyledButton
          component="label"
          onDrop={handleDrop}
          onDragOver={(e: React.DragEvent<HTMLButtonElement>) =>
            e.preventDefault()
          }
        >
          <CloudUploadIcon fontSize="large" />
          <Typography sx={{ color: "#000" }}>
            Drag and Drop files here
          </Typography>
          <Typography sx={{ color: "#9d9d9d" }}>or click to browse</Typography>
          <VisuallyHiddenInput
            type="file"
            accept="image/*"
            onChange={handleFileUpload}
            multiple
          />
        </StyledButton>
        {localFiles.length > 0 && (
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              width: "100%",
            }}
          >
            <Paper
              sx={{
                width: "100%",
                minHeight: "40px",
                display: "flex",
                flexWrap: "wrap",
                listStyle: "none",
                p: 0.5,
                my: 1.5,
              }}
              component="ul"
              variant="outlined"
            >
              {localFiles.map((fileData, index) => (
                <Chip
                  key={index}
                  label={fileData.file.name}
                  icon={<ImageOutlinedIcon />}
                  onDelete={() => removeFile(index)}
                  sx={{ margin: 0.5 }}
                />
              ))}
            </Paper>
            <Button
              sx={{
                backgroundColor: "#0095f6",
                textTransform: "none",
                "&:hover": {
                  backgroundColor: "#1877f2",
                },
                minWidth: "100px",
                "&.Mui-disabled": {
                  backgroundColor: "#0095f6",
                  color: "white",
                },
              }}
              variant="contained"
              onClick={handleSubmit}
              disabled={loading}
            >
              {loading ? (
                <CircularProgress size={24} color="inherit" />
              ) : (
                "Submit"
              )}
            </Button>
          </Box>
        )}
      </Box>
    </>
  );
}
